$nav-links-border-bottom: 4px;
.navbar {
	background: white;
	//margin-bottom: 1rem
	a {
		font-family: $customFont;
		font-size: 1.2rem;
		text-transform: uppercase;
		font-weight: 800; } }
.navbar-nav {
	li > a {
		padding: 1em 0 .5em 0; } }
.navbar-brand {
	padding: 0 1em 0 0;
	border-bottom: $nav-links-border-bottom solid transparent;
	svg {
		width: 7rem; } }
$doubleSection: false;

.navbar-nav {
	@if $doubleSection {
		li {
			a {
				border-bottom: $nav-links-border-bottom solid white; } }
		li:nth-child(1) {
			a.nav-link {
				color: $wdwBlue;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwBlue; } } }
		li:nth-child(2) {
			a.nav-link {
				color: $wdwBlue3;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwBlue3; } } }
		li:nth-child(3) {
			a.nav-link {
				color: $wdwPurple;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwPurple; } } }
		li:nth-child(4) {
			a.nav-link {
				color: $wdwPurple3;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwPurple3; } } }
		li:nth-child(5) {
			a.nav-link {
				color: $primaryColor;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $primaryColor; } } }
		li:nth-child(6) {
			a.nav-link {
				color: $wdwGreen;
				border-bottom: $nav-links-border-bottom solid white;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwGreen; } } } }
	@else {
		li:nth-child(1) {
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwBlue;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwBlue; } } }
		li:nth-child(2) {
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwPurple;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwPurple; } } }
		li:nth-child(3) {
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $primaryColor;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $primaryColor; } } }
		li:nth-child(4) {
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwOrange;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwOrange; } } }
		li:nth-child(5) {
			a.nav-link {
				border-bottom: $nav-links-border-bottom solid white;
				color: $wdwGreen;
				&:hover {
					border-bottom: $nav-links-border-bottom solid $wdwGreen; } } } } }



