article, .offerCard {
	max-width: 60rem;
	margin: 0 auto; }
@supports(display: grid ) {
	.offerCard {
		//border: 1px solid blue
		display: grid;
		padding: 1rem;
		grid-template-rows: 1fr;
		grid-template-columns: 6em 1fr;
		//grid-template-columns: 90px minmax(1fr 30px)
		//grid: 6em 1fr/  6em 1fr
 }		//border-radius: 5px

	.fig {
		grid-column: 1 / 2;
		grid-row: 1 / 2;
		justify-self: start; //start | end | center | stretch
		align-self: start;
		//border: 1px solid red
		img {
			width: 90px; } }
	.head {
		//border: 1px solid red
		grid-column: 2 / 3;
		grid-row: 1 / 2;
		padding: 0 .5em;
		align-self: center; }
	.cta {
		grid-column: 1 / 3;
		grid-row: 3 / 4;
		display: grid;
		grid-template-columns: 1fr 1fr;
		padding-top: .5rem;
		&--pn {
			//border: 1px solid red
			justify-self: stretch;
			grid-column: 1 / 2; }
		&--rm {
			//border: 1px solid red
			justify-self: stretch;
			grid-column: 2 / 3; }

		a {
			text-align: center;
			//flex-grow: 1

			padding: .5em;
 }			//width: calc(50% - .5rem)
		.rating {
			border-top: 1px solid $wdwGrey3;
			grid-column: 1 / 3;
			text-align: center; }
		svg {
			padding-bottom: .2em; }


		a:first-child {} }
	.desc {
		grid-column: 1 / 3;
		grid-row: 2 / 3;
		margin: .5em 0 0; }
	.tac {
		grid-column: 1 / 3;
		grid-row: 4 / 5; }

	@media (min-width: 700px) {
		//article
		.offerCard {
			//border: 1px solid red
			display: grid;
			grid-template-columns: 90px 1fr 120px;
			grid-template-rows: 1fr;
 }			//grid-template-columns: minmax(1fr 90px) 1fr
		.fig {
			grid-column: 1 / 2;
			grid-row: 1 / 2; }
		.head {
			grid-column: 2 / 3;
			grid-row: 1 / 2; }
		.desc {
			grid-column: 1 / 3;
			grid-row: 2 / 3;
			margin: .5em 1em 0 0; }
		.cta {
			grid-column: 3 / 4;
			grid-row: 1 / 3;
			display: flex;
			justify-content: center;
			flex-direction: row-reverse;
			flex-direction: column;
			padding-top: 0;
			.rating {
				display: block;
				border-top: none;
				padding: 0; }
			a {
				width: auto;
				align-self: stretch; }
			a:first-child {
				margin: 0; }
			a:nth-child(2) {
				margin: 0 0 .5em 0; } } }
	//.tac
	//	grid-column: 1 / 4
 }	//	grid-row: 3 / 4
