
//article
.offerCard {
	border-radius: 5px;
	box-shadow: 0px 10px 5px -5px rgba(blue, 0.05);
	h2 {
		font-family: $customFont;

		& a {
			//color: $wdwBlack1
			font-size: 1.2rem;
			color: $wdwBlack4;
			text-decoration: none;
			font-weight: normal; }
		small {
			color: $wdwBlack4;
			font-style: italic;
			font-size: 1.2rem;
			color: $wdwBlack; } }
	.offer {
		color: $wdwBlue2;
		font-family: $customFont;
		font-weight: 800;
		line-height: 1.8rem; }
	.cta--pn {
		text-decoration: none; }
	.cta--rm {
		border: none;
		font-style: italic; }
	.fig {
		img {
			border-radius: 10%; } } }
.tac {
	color: $wdwGrey1;
	font-family: $customFont; }
