@supports(display: grid ) {
	.offerCard--noDescription {
		max-width: 60rem;
		margin: 0 auto;
		display: grid;
		padding: 1rem;
		//grid-template-columns: 90px 1fr
		grid-template-rows: 1fr;
		grid-template-columns: 6em 1fr;
		.fig {
			//border: 1px solid red
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			justify-self: center; //start | end | center | stretch
			align-self: center; }
		.desc {
			grid-column: 1 / 3;
			grid-row: 1 / 3;
			margin: .5em 1em 0 0; } }

	@media (min-width: 700px) {
		//article
		.offerCard--noDescription {
			display: grid;
			grid-template-columns: 90px 1fr 120px;
			.fig {
				grid-column: 1 / 2;
				grid-row: 1 / 3; } } }

	//.tac
	//	grid-column: 1 / 4
 }	//	grid-row: 3 / 4
