.icon-area {
	background: white;
	padding: 2em 0; }
.iconNav {
	max-width: 80em;
	margin: 0 auto;
	list-style: none;
	display: grid;
	padding: 0;
	grid-template-rows: 1fr;
	grid-template-columns: repeat(2, minmax(5em, 1fr));
	@media (min-width: 650px) {
		grid-template-rows: 1fr;
		grid-template-columns: repeat(3, minmax(5em, 1fr)); }
	@media (min-width: 950px) {
		grid-template-rows: 1fr;
		grid-template-columns: repeat(6, minmax(5em, 1fr)); }
	li {
		padding: 1em;
		grid-auto-flow: column;
		//border: 1px solid red
		justify-self: center; //start | end | center | stretch
		text-align: center;
		@media (min-width: 700px) {
			padding: 0; } }
	li:nth-child(1) {
		a {
			color: $wdwBlue;
			&:hover {
				color: $wdwBlue3; } } }
	li:nth-child(2) {
		a {
			color: $wdwPurple;
			&:hover {
				color: $wdwPurple3; } } }
	li:nth-child(3) {
		a {
			color: $primaryColor;
			&:hover {
				color: $primaryColor3; } } }
	li:nth-child(4) {
		a {
			color: $wdwOrange;
			&:hover {
				color: $wdwOrange3; } } }
	li:nth-child(5) {
		a {
			color: $wdwGreen;
			&:hover {
				color: $wdwGreen3; } } }
	li:nth-child(6) {
		a {
			color: $wdwBlack;
			&:hover {
				color: $wdwBlack3; } } } }

