@supports(display: grid ) {
	.wdwcontent-left {
		padding: 2em 0;
		display: grid;
		//grid-gap: 1em
		grid-template-columns: 1fr;
		main {
			justify-self: center;
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			padding: 0 1em; }
		.side {
			grid-column: 1 / 2;
			grid-row: 2 / 3;
			//border: 1px solid $wdwGrey1
 } } }			//background: white

@media (min-width: 55rem) {
	@supports(display: grid ) {
		.wdwcontent-left {
			display: grid;
			//grid-gap: 1em
			grid-template-columns: 1fr 20em;
			main {
				justify-self: center;
				grid-column: 1 / 2;
				grid-row: 1 / 2; }
			.side {
				grid-column: 2 / 3;
				grid-row: 1 / 2;
				//border: 1px solid $wdwGrey1
 } } } }				//background: white

@media (min-width: 65rem) {
	@supports(display: grid ) {
		.wdwcontent-left {
			grid-template-columns: 1fr 25em; } } }


