$offers-fig-height-large: 10em;
$offers-fig-height-small: 7em;
$gridSupport: grid;

@supports(display: $gridSupport) {
	.hero-area_offer {
		width: 90vw;
		max-width: 90rem;
		margin: 0 auto;
		padding: 0;
		//border: 1px solid red
		display: grid;
		grid-gap: 2rem;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr;
		//text-align: center
		//justify-self: stretch

		@media (min-width: 30rem) {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: 1fr;
			li:nth-child(1) {
				grid-column: 1 / 2;
				grid-row: 1 / 2; }
			li:nth-child(2) {
				grid-column: 2 / 3;
				grid-row: 1 / 2; }
			li:nth-child(3) {
				grid-column: 1 / 2;
				grid-row: 2 / 3; }
			li:nth-child(4) {
				grid-column: 2 / 3;
				grid-row: 2 / 3;
 } }				//justify-self: stretch
		@media (min-width: 55rem) {
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-rows: 1fr;
			li:nth-child(1) {
				grid-row: 1 / 2;
				grid-column: 1 / 2; }
			li:nth-child(2) {
				grid-row: 1 / 2;
				grid-column: 2 / 3; }
			li:nth-child(3) {
				grid-row: 1 / 2;
				grid-column: 3 / 4; }
			li:nth-child(4) {
				display: none; } } }
	.offers {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr auto;
		//border: 1px solid red
		.card {
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			//align-self: center
			//justify-self: stretch
			height: 100%;
 }			//border: 1px solid blue
		.tac {
			//border-top: 1px solid red
			//justify-self: stretch
			//align-self: top
			grid-column: 1 / 2;
			grid-row: 2 / 3; } }
	.card {

		//max-width: 26em
		justify-self: stretch;
		display: grid;
		//margin: 0 auto
		grid-template-columns: 1rem 1fr 1fr 1rem;
		grid-template-rows: $offers-fig-height-small auto 1fr auto;
		//border: 1px solid red
		//@media (min-width: 55rem)
		//	grid-template-columns: 1rem minmax(5em, 1fr) minmax(5em, 1fr) 1rem
		@media (min-width: 55rem) {
			grid-template-rows: $offers-fig-height-large auto 1fr auto; }
		> * {
			//align-self: start
 }			//border: 1px solid red
		.fig {
			display: grid;
			height: $offers-fig-height-small;
			text-align: center;
			justify-self: stretch;
			grid-column: 1 / 6;
			grid-row: 1 / 2;
			@media (min-width: 55rem) {
				height: $offers-fig-height-large; }
			img {
				justify-self: center;
				align-self: center; } }
		h2 {
			align-self: start;
			grid-column: 2 / 4;
			grid-row: 2 / 3; }
		.offer-txt {
			align-self: start;
			//width: 100%
			grid-column: 2 / 4;
			grid-row: 3 / 4; }
		.cta--pn, .cta--rm {
			justify-self: stretch;
			align-self: center;
			text-align: center;
			grid-row: 4 / 5; }
		.cta--pn {
			grid-column: 2 / 3; }

		.cta--rm {
			grid-column: 3 / 4; } }

	.hero-area_offer {
		.tac {
			margin: 1rem 0 0 0;
			text-align: center; } } }
